import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from '@angular/common/http';
import { LoanInfo } from '../loan.model';

@Injectable()
export class ListService{
    loanInfo:LoanInfo[]=[];
constructor(private http:HttpClient){
} 
getLoanInfos(pageNum){
     return this.http.get<LoanInfo[]>("http://localhost:8090/loanInfo/list?pageNum="+pageNum);
}

getPageLimit(){
    return this.http.get<LoanInfo>("http://localhost:8090/loanInfo/pageLimit");
}
}