import { MdbTablePaginationComponent, MdbTableService } from 'angular-bootstrap-md';

import { Component, OnInit, ViewChild, HostListener, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { ListService } from './list.service';
import { LoanInfo } from '../loan.model';
import {Router} from  '@angular/router';
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  loanInfo=[];
  index=[];
  totalPages;
  firstIndex=1;
  lastIndex;
  pageItem="page-item";
  disabled="disabled";

  headElements = ['ID','Title','Employee Title', 'Member Id', 'Loan Amnt', 'Funded Amnt Inv','Term','Int Rate'];


  constructor(private tableService: MdbTableService,
    private cdRef: ChangeDetectorRef,private listService:ListService,
    private router: Router) {
  }

  ngOnInit() {
   this.listService.getLoanInfos(1).subscribe(data=>this.loanInfo=data);
   this.listService.getPageLimit().subscribe(data=>this.totalPages=data.pageLimit);
   if(this.totalPages<=10){
     this.lastIndex=this.totalPages;
   }else{
    this.lastIndex=10;
   }
   for(var i=this.firstIndex;i<=this.lastIndex;i++){
     this.index.push(i);
   }
  }

  nextClick(){
    if(this.lastIndex+10<=this.totalPages){
      this.index=[];
      for(var i=this.firstIndex+10;i<=this.lastIndex+10;i++){
        this.index.push(i);
      }
      this.firstIndex+=10;
      this.lastIndex+=10;
    }
  }
  prevClick(){
  if(this.firstIndex-10>=1){
    this.index=[];
    for(var i=this.firstIndex-10;i<=this.lastIndex-10;i++){
      this.index.push(i);
    }
    this.firstIndex-=10;
    this.lastIndex-=10;
  }
  }
  
  changePage(pageNum){
    this.listService.getLoanInfos(pageNum).subscribe(data=>this.loanInfo=data);
  }
  
  navigate(id){
  this.router.navigate(['/form',id]);
  }
}